<template>
  <div>
    <van-sticky>
      <van-nav-bar title="订单详情" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 地址 -->
    <div class="address-container">
      <img class="location-img" src="@/assets/imgs/mall/location.png" />
      <div class="address-info">
        <div class="address-info-top">
          <span>{{orderDetail.name}}</span>
          <span>{{orderDetail.phone}}</span>
        </div>
        <div class="address-info-bottom van-ellipsis">
         {{orderDetail.city}}{{orderDetail.detail}}
        </div>
      </div>
    </div>
    <!-- 商品 -->
    <div
      class="order-item-wrapper"
      @click="$router.push({ name: 'Order-detail' })"
    >
      <div class="order-header">
        <span>订单号:{{orderDetail.order_num}}</span>
      </div>
      <div class="order-body" v-for="item in orderDetail.orderdetail">
        <div class="order-body-pic">
          <van-image :src="item.path"></van-image>
        </div>
        <div class="order-body-info">
          <div class="order-body-info-name">{{item.goods.goods_name}}</div>
          <div class="order-body-info-num">数量：x{{item.nums}}</div>
        </div>
      </div>
      <div class="order-bottom">
        <div class="order-bottom-ctr">
          <div class="leftEl itemNum">共{{orderDetail.orderdetail.length}}件商品</div>
          <div class="rightEl itemPrice">
            实付款:<span>￥</span><span>{{orderDetail.cash}}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 优惠 -->
    <div class="payMid" @click='toLogi'>
      <div class="midItem">
        优惠券抵扣
        <span class="rightEl">已优惠<span class="midNum">{{orderDetail.ticket}}元</span></span>
      </div>
      <div class="midItem">
        积分抵扣
        <span class="rightEl">已抵扣<span class="midNum">{{orderDetail.integral}}积分</span></span>
      </div>
      <div class="midItem">
        佣金抵扣
        <span class="rightEl">已抵扣<span class="midNum">{{orderDetail.commission}}佣金</span></span>
      </div>
      <div class="midItem">
          运费
          <span class="rightEl"><span class="midNum">{{orderDetail.freight}}元</span></span>
        </div>
    </div>
    <!-- 订单信息 -->
    <div class="orderInfo">
      <div class="infoTitle">
        订单信息
      </div>
      <div class="infoCon">
        <p>订单编号:{{orderDetail.order_num}}</p>
        <p>
          支付方式:积分付款
        </p>
        <p>下单时间:{{orderDetail.create_time}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import {orderDetail} from "@/api/order"
export default {
  data() {
    return {
      orderDetail:{}
    };
  },
  created(){
     this.id=this.$route.query.id;
     this.getDetails(this.id);
  },

  methods: {
    getDetails(){
      orderDetail(this.id).then(res=>{
            this.orderDetail=res;
           res.orderdetail.forEach((item)=>{
             console.log(item);
             this.totalPrice=item.price*item.nums;
             item.path=item.goods.imgs.split(',')[0];
           })
      })
    },
    
    onClickLeft() {
      this.$router.go(-1);
    },
    toLogi(){
      this.$router.push('logistics');
    }
  }
};
</script>
<style lang="less" scoped>
.address-container {
  background: white;
  padding: 15px 10px 15px 8px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 3px 1px #eee;
  width: 345px;
  box-sizing: border-box;
  margin: 10px auto;
  .location-img {
    width: 36px;
    height: 36px;
    margin-right: 10px;
  }

  .address-info {
    width: 247px;
    line-height: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-size: 12px;

    .address-info-top {
      > span:first-child {
        margin-right: 20px;
      }
      > span:last-child {
        color: #a8a8a8;
      }
    }
  }

  .no-default-address {
    font-size: 12px;
    color: #a8a8a8;
    width: 267px;
  }

  .arrow-icon {
    font-size: 16px;
  }
}
.order-item-wrapper {
  width: 325px;
  margin: 15px auto;
  border-radius: 10px;
  box-shadow: 0 0 15px 2px #f5f5f5;
  background-color: #fff;
  padding: 0 15px;
  font-size: 13px;

  .order-header {
    height: 34px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f6f3f3;
  }

  .order-body {
    box-sizing: border-box;
    height: 120px;
    padding: 10px 0;
    border-bottom: 1px solid #f6f3f3;

    &-pic {
      width: 100px;
      height: 100px;
      float: left;
      margin-right: 15px;
      background-color: #eee;

      .van-image {
        width: 100%;
        height: 100%;
      }
    }

    &-info {
      float: left;
      width: 200px;
      height: 100px;

      &-name {
        height: 50px;
        line-height: 25px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }

      &-price {
        height: 25px;
        line-height: 25px;
        font-size: 12px;
        color: #ff6666;
      }

      &-num {
        line-height: 25px;
        font-size: 10px;
      }
    }
  }

  .order-bottom {
    height: 36px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-ctr {
      width: 100%;
      .itemNum {
        color: #333;
        font-size: 12px;
      }
      .itemPrice {
        span:last-child {
          color: #de4f53;
          font-size: 18px;
        }
      }
      .van-button {
        margin-left: 15px;
        padding: 0 10px;
      }
    }
  }

  .orange-color {
    color: #e94c04;
    border-color: #e94c04;
  }
}
.payMid {
  box-shadow: 0 0 6px 1px #f5f5f5;
  width: 345px;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 4px 0 4.5px 0;
  margin: 15px auto;
  font-size: 15px;
  color: #333;
  .midItem {
    margin: 10px 14px 0 11.5px;
    padding-bottom: 14px;
    border-bottom: 1px solid #dcdcdc;
    .midNum {
      color: #de4f53;
      font-size: 15px;
      margin-left: 2px;
    }
  }
  .midItem:last-child {
    border-bottom: 0 none;
  }
}
.orderInfo {
  margin: 0 auto;

  box-sizing: border-box;
  width: 345px;
  border-radius: 10px;
  height: 153px;
  box-shadow: 0 0 6px 1px #f5f5f5;
  color: #333;
  font-size: 14px;
  
  .infoTitle {
    padding: 16px 20px 12px 15px ;
    font-size: 16px;
    border-bottom: 1px solid #bfbfbf;
  }
  .infoCon{
    padding: 0 0 16px 15px;
  }
}
</style>
