import request from '@/utils/request'
export function orderList(status) {
    return request({
        url: 'api/orders/orderList',
        method: 'get',
        params:{status}
    })
}
export function orderDetail(id) {
    return request({
        url: 'api/orders/orderDetail',
        method: 'get',
        params:{id}
    })
}
// 取消订单
export function cancelOrders(id) {
    return request({
        url: 'api/orders/cancelOrders',
        method: 'post',
        params:{id}
    })
}
// 确认收货
export function sureOrder(id) {
    return request({
        url: 'api/orders/sure',
        method: 'post',
        params:{id}
    })
}
// 物流信息
export function logisticsInfo(id) {
    return request({
        url: 'api/orders/logisticsInfo',
        method: 'get',
        params:{id}
    })
}
// 重新支付
export function payOrder(id) {
    return request({
        url: 'api/wechat/payOrder',
        method: 'post',
        params:{id}
    })
}
export function orderNums() {
    return request({
        url: 'api/orders/orderNums',
        method: 'get'
    })
}
